<template>
  <div class="home">
    <div class="form_box class ='p-3'">
      <b-row align-h="center" class="pt-3">
        <b-col cols="8">
          <b-row>
            <h1><strong>Register</strong></h1>
          </b-row>
        </b-col>
      </b-row>
      <b-row align-h="center">
        <b-col cols="8">
          <b-form @submit="onSubmit" @reset="onReset">
            <b-form-group>
              <b-form-input
                class="m-3"
                id="email_input"
                v-model="form.email"
                type="email"
                placeholder="Enter email"
                required
              ></b-form-input>

              <b-form-input
                class="m-3"
                id="username_input"
                v-model="form.name"
                placeholder="Enter Username"
                required
              ></b-form-input>

              <b-form-input
                class="m-3"
                id="password_input"
                v-model="form.password"
                placeholder="Enter Password"
                type="password"
                required
              ></b-form-input>

              <b-form-input
                class="m-3"
                id="password_input"
                v-model="form.confirm_password"
                placeholder="Confirm Password"
                type="password"
                required
              ></b-form-input>
              I confirm I have read, understand and accept the <br />
              <a href="">Privacy Policy</a>,
              <a href="">Data Usage Policy</a> and <a href="">Cookie Policy</a>
              <b-form-checkbox
                class="m-1"
                id="checkbox-1"
                v-model="form.privacy_policy"
                name="checkbox-1"
                value="accepted"
                unchecked-value="not_accepted"
                switch
              >
              </b-form-checkbox>
            </b-form-group>

            <b-button type="submit" variant="success" class="m-3"
              >Register</b-button
            >
            <b-button type="reset" variant="danger" class="m-3">Reset Form</b-button>

            <p>
              Already have an account?
              <router-link to="/">Login</router-link>
            </p>
          </b-form>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import router from "@/router";

export default {
  name: "RegisterView",
  components: {},
  data() {
    return {
      form: {
        email: "",
        name: "",
        password: "",
        privacy_policy: false,
        data_policy: false,
        cookie_policy: false,
        confirm_password: "",
      },
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      router.push({ path: "/" });
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      this.form.password = "";
    },
  },
};
</script>
