<template>
  <div class="home">
    <div class="form_box class ='p-3'">
      <b-row align-h="center" class="pt-3">
        <b-col cols="8">
          <b-row>
            <h1><strong>Login</strong></h1>
          </b-row>
        </b-col>
      </b-row>
      <b-row align-h="center">
        <b-col cols="8">
          <b-form @submit="onSubmit" @reset="onReset">
            <b-form-group>
              <b-form-input
                class="m-3"
                id="username_input"
                v-model="form.name"
                placeholder="Enter Username"
                required
              ></b-form-input>

              <b-form-input
                class="m-3"
                id="password_input"
                v-model="form.password"
                placeholder="Enter Password"
                type="password"
                required
              ></b-form-input>
            </b-form-group>

            <p>
              Don't have an account?
              <router-link to="/register">Register</router-link>
            </p>

            <b-button pill type="submit" variant="success" class="m-3"
              >Login</b-button
            >
          </b-form>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import router from "@/router";

export default {
  name: "LoginView",
  components: {},
  data() {
    return {
      form: {
        name: "",
        password: "",
      },
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      router.push({ path: "/dashboard" });
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      this.form.password = "";
    },
  },
};
</script>
<style>
.form_box {
  outline: 3px ridge #004c18;
  outline-offset: 0px;

  -webkit-border-radius: 20px;
  border-radius: 20px;
  background-color: rgba(0, 46, 15, 0.75);
}
</style>